html{
    min-width: 450px;
}
body{
    min-width: 450px;
    display: flex;
    justify-content: center;
    position: relative;
    min-width: 450px;

    header{
        min-width: 450px;
        width: 100%;
        height: 120px;
        background-color: blueviolet;
        padding: 10px 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        .logo{
            display: inline-block;
            color: white;
            font-size: 35px;
            font-weight: 700;
            margin: 0 20px;
            min-width: 330px;
        }
        .search-bar{
            display: flex;
            flex-wrap: nowrap;
            min-width: 330px;
            justify-content: center;
            align-items: center;
            width: calc(100% - 330px);

            input{
                width: 30%;
                margin-left: 10px;
                height: 50px;
                box-sizing: border-box;
                font-size: 25px;
            }
            select{
                height: 50px;
            }
        }
        button.search{
            height: 50px;
            margin-right: 10px;
            background-color: blueviolet;
            color: white;
            font-size: 30px;
            border: 3px solid white;
            border-radius: 10px;
            box-shadow: 3px 3px 3px 3px rgba(0,0,0,0.3);
        }
        button.search:hover{
            box-shadow: 5px 5px 5px 5px rgba(0,0,0,0.5);
            color: rgba(255,255,255,0.7);
            border: 3px solid rgba(255,255,255,0.7);
        }
    }
    main{
        top: 150px;
        position: absolute;
        min-width: 450px;
        .movies{
            width: 80%;
            background-color: white;
            // border: 3px solid blueviolet;
            padding: 20px 40px 200px 40px;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            margin: 20px auto;
            border-radius: 10px;

            .movie{
                width: 250px;
                margin: 20px;
                position: relative;
                height: 370px;
                box-shadow: 3px 3px 3px 3px rgba(0,0,0,0.3);
                border-radius: 10px;
                cursor: pointer;

                
                
                img{
                    max-width: 100%;
                    max-height: 100%;
                    min-width: 100%;
                    min-height: 100%;
                    position: absolute;
                    top: 0;
                    border-radius: 10px;
                    
                }
                h2{
                    z-index: 1;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    padding: 20px;
                    margin: 0 auto;
                    box-sizing: border-box;
                    width: 100%;
                    text-align: center;
                    color: white;
                    font-weight: 700;
                    font-size: 15px;
                    background-color: rgba(138, 43, 226,0.3);
                    border-radius: 0 0 10px 10px;
                }
                div.year{
                    z-index: 1;
                    position: absolute;
                    bottom: 55px;
                    left:0;
                    right: 0;
                    padding: 10px 10px 0 10px;
                    margin: 0 auto;
                    box-sizing: border-box;
                    width: 100%;
                    text-align: center;
                    color: white;
                    font-size: 13px;
                    background-color: rgba(138, 43, 226,0.3);
                }

                
            }
            .movie:hover{
                width: 270px;
                height: 390px;
                box-shadow: 5px 5px 5px 5px rgba(0,0,0,0.5);
                transition: 0.3s;
                h2{
                    font-size: 17px;
                    font-weight: 900;
                    background-color: rgba(138, 43, 226,0.5);
                }
                div.year{
                    font-size: 15px;
                    font-weight: 700;
                    background-color: rgba(138, 43, 226,0.5);
                    bottom: 57px;
                }
            }
        }
        .action{
            position: absolute;
            bottom: 0;
            width: 100%;
            height: 200px;
            display: flex;
            justify-content: center;
            align-items: center;
            button.more{
                height: 50px;
                margin-right: 10px;
                background-color: blueviolet;
                color: white;
                font-size: 30px;
                border: 3px solid white;
                border-radius: 10px;
                box-shadow: 3px 3px 3px 3px rgba(0,0,0,0.3);
                }
            button.more:hover{
                box-shadow: 5px 5px 5px 5px rgba(0,0,0,0.5);
                color: rgba(255,255,255,0.7);
                border: 3px solid rgba(255,255,255,0.7);
                }

        }


    }

    div.modal{
        min-width: 450px;
        display: none;
        width: 80%;
        height: 100%;
        padding: 40px 20px 20px;
        background-color: white;
        border: 3px solid blueviolet;
        border-radius: 15px;
        position: fixed;
        box-shadow: "0 4px 8px 1 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.2)";
        z-index: 9999;
        top: 0;
        right: 0;
        left: 0;
        box-sizing: border-box;
        overflow: auto;
        margin: 0 auto;
        -ms-overflow-style: none;
        &::-webkit-scrollbar{
            display: none !important;
        }
        .modal-flex{
            width: 100%;
            height: max-content;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
            .movie-title{
                font-size: 35px;
                font-weight: 700;
                text-align: center;
                padding-bottom: 20px;
                width: 100%;
            }
            img{
                max-width: 350px;
                min-width: 350px;
                margin: 0 100%;
                border-radius: 10px;
            }
            .movie-date{
                font-size: 13px;
                background-color: blueviolet;
                color: white;
                border-radius: 5px;
                padding: 10px;
                margin: 10px;
            }
            .movie-time{
                font-size: 13px;
                background-color: blueviolet;
                color: white;
                border-radius: 5px;
                padding: 10px;
                margin: 10px;
            }
            .movie-plot{
                padding: 10px;
                margin: 0 100%;
                max-width: 100%;
                min-width: 100%;
                line-height: 20px;
            }
            h3{
                font-size: 25px;
                font-weight: 700;
                color: blueviolet;
                position: absolute;
                top:0;
                left: 0;
                padding: 10px;
                text-align: center;
                width: 100%;
                box-sizing: border-box;
            }
            .movie-actor{
                width: auto;
                box-sizing: border-box;
                position: relative;
                padding: 50px 20px 20px;
                margin: 10px 20px 20px;
                text-align: center;
                min-width: 300px;
            }
            .movie-director{
                width: auto;
                box-sizing: border-box;
                position: relative;
                padding: 50px 20px 20px;
                margin: 10px 20px 20px;
                text-align: center;
                min-width: 300px;
            }
            .movie-production{
                width: auto;
                box-sizing: border-box;
                position: relative;
                padding: 50px 20px 20px;
                margin: 10px 20px 20px;
                text-align: center;
                min-width: 300px;
            }
            .movie-genre{
                width: auto;
                box-sizing: border-box;
                position: relative;
                padding: 50px 20px 20px;
                margin: 10px 20px 20px;
                text-align: center;
                min-width: 300px;
            }


        }
        
        .material-symbols-outlined{
            position: absolute;
            top: 10px;
            right: 10px;
            width: 20px;
            height: 20px;
            cursor: pointer;
            color: blueviolet;
        }
    }
    
    div.modal-background{
        z-index: 9998;
        position: fixed;
        left: 0px;
        top: 0px;
        width: 100%;
        height: 100%;
        overflow: hidden;
        background-color: rgba(0,0,0,0.3)
    }
}


